
.detail{
	width:100%;
    height:800px;
    border:1px solid #c3c;
	overflow:hidden;
}
.d-left{
	width:15%;
	height:700px;
	overflow: hidden;
	float:left;
	background: #fff;
}
.d-right{
	width:83%;
	height:800px;
	overflow:auto;
	padding:10px;
	float:right;
	background: #fff;
}
.d-left .img{
	width:90px;
	height:90px;
	margin:0 auto;
	position: relative;
}
.d-left .list li{
	line-height: 25px;
	font-size: 14px;
	color:#666;
	text-align: center;
	cursor: pointer;
}
.active{
	color:#0099ff!important;
}
.d-left .img li{
	width:90px;
	height:90px;
	padding:10px;
	position: absolute;
	top:0;
	left:0;
}
.d-left .img li img{
	width:70px;
	height:70px;
}



